import React from 'react';
import {generateName} from './constant/Randomname';
import { Link,Box,Typography,Container,Button, Divider } from '@material-ui/core';
import { CardList } from './components/card-list/card-list';
import { SearchBox } from './components/search-box/search-box';
import './App.css';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
    {'Copyright  '}
      <Link color="inherit" href="https://rhivent.github.io">
        Riventus
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

export default class App extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      randomname:[],
      searchField:''
    };
  }

  getRandomName = async () => {
    let data=[];
    for (let index = 1; index < 20; index++) {
      await data.push({
        name:generateName(),
        id:index
      });  
    }
    await this.setState({
      randomname:data
    });
  }

  handleChange = data => {
    this.setState({
      searchField:data.target.value
    },()=>{
      // console.log(this.state.searchField);
    });
    return true;
  }
  
  render(){
    let filterRN;
    if(this.state.randomname.length > 0){
      const {randomname,searchField} = this.state;
      filterRN = randomname.filter( n => {
        return n.name.toLowerCase().includes(searchField.toLowerCase())
      })
    }
    return (
      <React.Fragment>
      <Container maxWidth="lg" className="App">
        <Box my={12}>
          <h1>
            Create React App
          </h1>
          { this.state.randomname.length > 0 &&(
          <React.Fragment>
            <SearchBox placeholder="Search Name" handleChange={this.handleChange} />
            <Divider style={{margin:'.5em',width:'85vw'}} />
            <CardList randomname={filterRN}></CardList>
          </React.Fragment>
            ) 
          }
          <Button onClick={this.getRandomName} style={{color:"#ddd"}}>
            Generate Random Name
          </Button>
          <Copyright />
          </Box>
        </Container>
      </React.Fragment>
    );
  }
}
