import React from 'react';
import {Typography} from '@material-ui/core';
import './card.css';
import ImageCard from '../../images/riventus.jpeg';

const ke_email = (data) => {
  let name;
  name = data.toLowerCase().split(" ").join("_");
  return name;
}

export const Card = props => {
  return(
    <div className="card-container">
    <img src={ImageCard} alt="gambarku" size="180" />
    <Typography variant="h6" align="center">{props.name}</Typography>
    <Typography variant="subtitle1" align="center">{ke_email(props.name)+"@ven.com"}</Typography>
    </div>
  );
}
